import type { RefObject } from 'preact';
import type {
  DiagramContextMenuConfig,
  DiagramContextMenuContext,
  ItemInfo
} from '../diagram.types';
import { type gestureTypes, useContextMenu } from '#hooks/PRIVATE_useVisContextMenu';
import { getDiagramContextMenuContext } from '../utils/contextMenuUtils';

type Props<K1, K2, D1, D2> = {
  nodesMap: Map<K1, D1>;
  linksMap: Map<K2, D2>;
  focusedItemInfo: ItemInfo<K1, K2>;
  rootRef: RefObject<HTMLDivElement>;
  contextMenuConfig?: DiagramContextMenuConfig<D1, D2>;
  onContextMenuDismissed?: (gesture: gestureTypes) => void;
};

export const useDiagramContextMenu = <K1, K2, D1, D2>({
  focusedItemInfo,
  rootRef,
  contextMenuConfig,
  onContextMenuDismissed,
  nodesMap,
  linksMap
}: Props<K1, K2, D1, D2>) => {
  const { contextMenuProps, contextMenuContent } = useContextMenu<
    DiagramContextMenuContext<D1, D2>
  >({
    onContextMenuHandler: ({ gesture, target }) => {
      let idx;
      let elementType: 'node' | 'link' | 'background' = 'background';
      const currentTarget = target as HTMLElement;
      if (gesture === 'keyboard') {
        idx = focusedItemInfo.id;
        if (focusedItemInfo.isNode) {
          elementType = 'node';
        } else if (idx !== undefined && !focusedItemInfo.isNode) {
          elementType = 'link';
        }
      } else if (
        (currentTarget.closest('[data-oj-private-node]') as HTMLElement)?.dataset['ojPrivateNode']
      ) {
        idx = (currentTarget.closest('[data-oj-private-node]') as HTMLElement)?.dataset[
          'ojPrivateNode'
        ] as K1;
        elementType = 'node';
      } else if (
        (currentTarget.closest('[data-oj-private-link]') as SVGElement)?.dataset[
          'ojPrivateLink'
        ] as K2
      ) {
        idx = (currentTarget.closest('[data-oj-private-link]') as SVGElement)?.dataset[
          'ojPrivateLink'
        ] as K2;
        elementType = 'link';
      }

      const { context } = getDiagramContextMenuContext<K1, K2, D1, D2>(
        nodesMap,
        linksMap,
        elementType,
        idx
      );
      if (gesture === 'keyboard') {
        let element;
        if (focusedItemInfo.isNode) {
          element = rootRef.current?.querySelector(`[data-oj-private-node="${idx}"]`);
        } else {
          element = rootRef.current?.querySelector(`[data-oj-private-link="${idx}"]`);
        }
        return {
          type: 'element',
          context,
          elem: element ? element : null
        };
      }
      return { type: 'pointer', context };
    },
    contextMenuOptions: {
      isDisabled: !contextMenuConfig //If there is not a context menu renderer we disable the hook
    },
    rootRef,
    contextMenuConfig,
    onContextMenuDismissed
  });

  return { contextMenuContent, contextMenuProps };
};
