import { ComponentChildren } from 'preact';
import type {
  States,
  LinkRendererContext,
  DiagramLinkData,
  DiagramLayoutOutput,
  Bounds
} from './diagram.types';
import { DiagramLinkElement } from './DiagramLinkElement';
import { layerStyles } from './themes/DiagramStyles.css';
import { getZoomToFitZoom } from './utils/diagramUtils';

type Props<K2, K1, D2, L> = {
  links: D2[];
  states: States<K1, K2>;
  previousStates: States<K1, K2>;
  linkRenderer: (context: LinkRendererContext<D2, L>) => ComponentChildren;
  width: number;
  height: number;
  supportsSelection: boolean;
  layoutOutput: L;
  combinedNodeBounds: Bounds;
};

export function DiagramLinkLayer<
  K2 extends string | number,
  K1 extends string | number,
  D2 extends DiagramLinkData<K2, K1> = DiagramLinkData<K2, K1>,
  L extends DiagramLayoutOutput<K1, K2> = DiagramLayoutOutput<K1, K2>
>({
  links,
  states,
  previousStates,
  linkRenderer,
  width,
  height,
  supportsSelection,
  layoutOutput,
  combinedNodeBounds
}: Props<K2, K1, D2, L>) {
  const zoom = getZoomToFitZoom(width, height, combinedNodeBounds);
  return (
    <svg class={layerStyles} width={width / zoom} height={height / zoom}>
      {links.map((link) => {
        const state = {
          selected: states.selectedLinkIds.includes(link.id),
          focused: states.focusedId === link.id,
          hovered: states.hoveredId === link.id,
          zoom: states.zoom
        };
        const previousState = {
          selected: previousStates.selectedLinkIds.includes(link.id),
          focused: previousStates.focusedId === link.id,
          hovered: previousStates.hoveredId === link.id,
          zoom: previousStates.zoom
        };
        return (
          <DiagramLinkElement
            id={link.id}
            label={link.label}
            state={state}
            previousState={previousState}
            isDimmed={
              states.highlightedIds.length !== 0 && !states.highlightedIds.includes(link.id)
            }
            activeId={states.activeId}
            accessibleLabel={link.accessibleLabel}
            data={link}
            linkRenderer={linkRenderer}
            supportsSelection={supportsSelection}
            layoutOutput={layoutOutput}
          />
        );
      })}
    </svg>
  );
}
