import type { DiagramContextMenuContext } from '../diagram.types.ts';

export function getDiagramContextMenuContext<K1, K2, D1, D2>(
  nodesMap: Map<K1, D1>,
  linksMap: Map<K2, D2>,
  type: 'node' | 'link' | 'background',
  idx?: K1 | K2
) {
  let context: DiagramContextMenuContext<D1, D2>;

  if (type === 'node') {
    context = {
      data: nodesMap.get(idx as K1)!,
      type
    };
  } else if (type === 'link') {
    context = {
      data: linksMap.get(idx as K2)!,
      type
    };
  } else {
    context = {
      type: 'background'
    };
  }

  return {
    context: context as DiagramContextMenuContext<D1, D2>
  };
}
