import type {
  DiagramProps,
  DiagramNodeData,
  DiagramLinkData,
  DiagramLayoutOutput
} from './diagram.types';
import { TrackResizeContainer } from '#UNSAFE_TrackResizeContainer';
import { dimensionStyle } from './themes/DiagramStyles.css';
import { DiagramWithDimensions } from './DiagramWithDimensions';

/**
 * Diagrams are used to display a set of nodes and the links between them.
 */
export function Diagram<
  K1 extends string | number,
  K2 extends string | number,
  D1 extends DiagramNodeData<K1> = DiagramNodeData<K1>,
  D2 extends DiagramLinkData<K2, K1> = DiagramLinkData<K2, K1>,
  L extends DiagramLayoutOutput<K1, K2> = DiagramLayoutOutput<K1, K2>
>({ width, height, ...otherProps }: DiagramProps<K1, K2, D1, D2, L>) {
  return otherProps.nodes && otherProps.nodes.length > 0 ? (
    <TrackResizeContainer width={width} height={height} class={dimensionStyle}>
      {(width, height) => <DiagramWithDimensions width={width} height={height} {...otherProps} />}
    </TrackResizeContainer>
  ) : null;
}
